#scrtop {
  position: fixed;
  bottom: 15px;
  right: 15px;
  display: none;
  background-color: $primary;
  border: 1px solid $white;
  padding: 5px 10px;
  border-radius: 5px;
  &:hover i{
    transform: translateY(-5px);
  }

  i {
    color: #FFFFFF;
    transition: .5s all ease;
  }
}
